import React from "react";

class Info extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            current: 0,
            helpArr: [],
            _id: null,
        };

        this.on = this.on.bind(this);
        this.off = this.off.bind(this);
        this.next = this.next.bind(this);
        this.prev = this.prev.bind(this);
    }

    static getDerivedStateFromProps(props, state) {
        let arr = [];
        let id;
        if (props.page === "home0") {
            arr.push("مرحبا بكم, سأعلمكم كيفية إستخدام الموقع بشكل سريع, هذه هي الصفحة الرئيسية, في كل صفحة يمكنكم الضغط على زر الإرشادات لكي أرشدكم على الصفحة, للتنقل بين إرشادات الصفحة إضغطوا على الأسهم وللخروج من الإرشادات إضغطوا على زر الخروج");
            arr.push("من هذه الصفحة يمكنكم إنشاء مهمة جديدة أو إستعمال مهام سابقة عن طريق الضغط على الزر الملائم");
            id = "overlayhome0";
        }
        if (props.page === "home-a") {
            arr.push("من هذه الصفحة تستطيعون إنشاء مهمة جديدة, يمكنكم الرجوع للخلف عن طريق الضغط على زر الرجوع");
            arr.push('لإنشاء مهمة جديدة أدخوا الجمل واحدة تلو الإخره داخل العلبة المكتوب بها "أدخل/ي الجملة" وبعدها إضغطوا على الزر "إدخال"');
            arr.push('بعد إدخال الجملة يمكنكم حذفها عن طريق الضغط على الزر "حذف" ويمكنكم حذف جميع الجمل عن طريق الضغط على الزر "حذف جميع الجمل"');
            arr.push("يمكنكم تغيير ترتيب الجمل عن طريق الضغط على الجملة التي تريدون تغييرها ومن ثم جرّها على مكان الجملة التي تريدون أن تكون مكانها");
            arr.push('من ثم إضغطوا على زر "التقدم" وبعدها أدخلوا الوقت الذي تريدونه داخل العلبة المكتوب بها "أدخل/ي الوقت بالثوان" ومن ثم إضغطوا على الزر "إدخال الوقت", ملاحظة: يجب أن يكون الوقت أكبر من صفر');
            arr.push('بعد الإنتهاء من إنشاء المهمة, يمكنكم حفظ المهمة لكي تستطيعوا إستعمالها لاحقا عن طريق الضغط على الزر "إحفظ/ي المهمة" أو الإنتقال الى صفحة الطلاب مباشرة بدون حفظها عن طريق الضغط على الزر "العبور الى صفحة الطلاب" لكي تبدأ المهمة بالعمل.ملاحظة: بالطبع, يمكنكم أيضا حفظ المهمة أولا ومن ثم العبور الى صفحة الطلاب.');
            id = "overlayhome-a";
        }
        if (props.page === "home-b") {
            arr.push('من هذه الصفحة تستطيعون تعديل المهمة, يمكنكم إضافة جملة جديدة الى المهمة عن طريق إدخالها داخل العلبة المكتوب بها "أدخل/ي جملة" ومن ثم الضغط على الزر "إدخال"');
            arr.push('يمكنكم حذف جملة من المهمة عن طريق الضغط على الزر "حذف" ويمكنكم حذف جميع الجمل عن طريق الضغط على الزر "حذف جميع الجمل"');
            arr.push("يمكنكم تغيير ترتيب الجمل عن طريق الضغط على الجملة التي تريدون تغييرها ومن ثم جرّها على مكان الجملة التي تريدون أن تكون مكانها");
            arr.push('يمكنكم تغيير الوقت عن طريق إدخال رقم جديد الى العلبة المسجل بها الوقت القديم أو مكتوب بها إدخل/ي الوقت بالثوان اذا قد محيتم الوقت المسجل بها, اذا أردتم تغيير الوقت يجب أن تضغطوا بعدها على الزر "إدخال الوقت"');
            arr.push('بعد الإنتهاء من تعديل المهمة, يمكنكم حفظ المهمة كمهمة جديدة, أي أن المهمة القديمة ستبقى وبالإضافة لها ستحفظ المهمة المعدلة للإستعمالات اللاحقة وذلك عن طريق الضغط على الزر "الحفظ كمهمة جديدة", ويمكنكم حفظ المهمة المعدلة مكان المهمة القديمة, أي أن المهمة القديمة ستمحى وستحل مكانها المهمة المعدلة, وذلك عن طريق الضغط على الزر "تعديل المهمة", أو يمكنكم الذهاب الى صفحة الطلاب مباشرة بدون حفظ المهمة المعدلة وذلك عن طريق الضغط على الزر "العبور الى صفحة الطلاب".ملاحظة: بالطبع, يمكنكم أيضا حفظ المهمة أولا ومن ثم العبور الى صفحة الطلاب.');
            id = "overlayhome-b";
        }
        if (props.page === "home2") {
            arr.push('من هذه الصفحة تستطيعون رؤية المهام القديمة المحفوظة, يمكنكم الرجوع الى الخلف عن طريق الضغط على الزر "الرجوع", يمكنكم حذف جميع المهام كليا عن طريق الضغط على الزر "حذف جميع المهام".ملاحظة: اذا حذفتم المهام لن تستطيعوا إسترجاعهن');
            arr.push('يمكنكم إختيار مهمة وجعلها تبدأ بالعمل عن طريق الضغط على الزر "إختيار" الظاهر تحت المهمة التي تريدون إختيارها, ومن ثم الضغط على الزر "العبور الى صفحة الطلاب"');
            arr.push('يمكنكم تعديل مهمة موجودة عن طريق الضغط على زر "تعديل" الظاهر تحت المهمة التي تودون تعديلها');
            arr.push('يمكنكم حذف مهمة موجودة عن طريق الضغط على الزر "حذف" الظاهر تحت المهمة التي تريدون حذفها. ملاحظة: اذا حذفتم مهمة لن تستطيعوا إسترجاعها');
            arr.push('اذا كان هنالك مشكلة في هذه الصفحه, جربوا الضغط على الزر "تحديث" الظاهر أعلى الصفحة, واذا بقيت المشكلة حاولوا الضغط على الزر "الرجوع" ومن ثم العودة الى هذه الصفحة');
            id = "overlayhome2";
        }
        if (props.page === "list") {
            arr.push('هذه الصفحة هي صفحة الطلاب, وهي الصفحة التي تعمل بها المهام, أولا, اذا اردتم الرجوع للخلف إضغطوا على الزر "الرجوع"');
            arr.push('لكي تبدأ المهمة, إضغطوا على الزر "إبدأ", حيث سيتم تلوين كلمات الجملة المعروضة واحدة تلو الأخرة بلون معين يتم إختياره من قبل البرنامج بشكل عشوائي');
            arr.push('للإنتقال للجملة التالية إضغطوا على الزر "الجملة التالية", وللعودة للجملة السابقة, إضغطوا على الزر "الجملة السابقة"');
            arr.push('يمكنكم توقيف الجملة عن العمل عن طريق الضغط على الزر ❚❚ اذا أردتم الإنتقال الى جملة مختلفة والجملة الحالية مازالت في منتصف العمل');
            arr.push('يمكنكم تكبير الخط عن طريق الضغط على الزر + أو تصغيره عن طريق الضغط على الزر -');
            id = "overlaylist";
        }
        return{
            helpArr: arr,
            _id: id,
        };
    }

    on() {
        document.getElementById(this.state._id).style.display = "block";
    }
      
    off() {
        document.getElementById(this.state._id).style.display = "none";
        this.setState({current: 0});
    }

    next() {
        if (this.state.current < this.state.helpArr.length - 1) {
            let newCur = this.state.current + 1;
            this.setState({ current: newCur });
        }
    }

    prev() {
        if (this.state.current > 0) {
            let newCur = this.state.current - 1;
            this.setState({ current: newCur });
        }
    }

    render() {
        const help = this.state.helpArr[this.state.current];
        return (
            <>
            <button className="info-btn" title="لإرشادك على الصفحة لإضغط هنا" onClick={this.on}>🛈إرشادات</button>
            <div id={this.state._id}>
                <button className="info-btns" onClick={this.off}>الخروج</button>
                <bdo dir="rtl" className="overlayText">{help}<br/><br/><b style={{ fontSize:"16px", color:"white" }}>{this.state.current+1} من {this.state.helpArr.length}</b></bdo>
                <button className="info-btns" onClick={this.next}>←</button>
                <button className="info-btns" onClick={this.prev}>→</button>
            </div>
            </>
        );
    }

}

export default Info;