import React from "react";

class BuildAssign extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            sentences: [],
            time: []
        };
    }

    static getDerivedStateFromProps(props, state) {
        return {
            sentences: props.assign.sents,
            time: props.assign.secs
        };
    }

    render() {
        const list = this.state.sentences;
        return (
            <div style={{ fontSize:"18px" }}>
                <h5 style={{marginLeft:"50%"}}>:الجمل</h5>
                {list.map((sentence, index) => {
                    return (
                        <span style={{ marginLeft:"50%", display:"block" }} key={index}>{sentence}</span>
                    )
                })}
                <h5 style={{marginLeft:"50%"}}>:الوقت</h5>
                <span style={{ marginLeft:"50%", display:"block" }}>{this.state.time}</span>
            </div>
        );
    }

}

export default BuildAssign;