import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import BuildAssign from "../Components/BuildAssign";
import Info from '../Components/Info';

function RefreshPage() {

    function refresh() {
        window.location.reload();
    }

    return (
        <>
        <button className="refresh-btn" onClick={refresh}>🔄تحديث</button>
        </>
    );
}

class Home2 extends Component {

    constructor(props) {
        super(props);
        this.state = {
            assignmentsArr: [],
            chosenAssign: {
                sents: [],
                secs: ""
            },
            doneFetching: false,
            doneChoosing: false,
            chosenId: -1
        };

        this.updateAssignments = this.updateAssignments.bind(this);
        this.chooseAssign = this.chooseAssign.bind(this);
        this.askAgain = this.askAgain.bind(this);
        this.askAgainAll = this.askAgainAll.bind(this);
    }

    componentDidMount() {
        this.updateAssignments();
    }

    getAssignments = async () => {
        const response = await fetch('/api/assigns', {
            method: "GET",
        })
        //console.log("response:");
        //console.log(response);
        if (response.status !== 200) {
            //console.log("error");
            window.location.reload();
        }
        //console.log("body:");
        const body = await response.json();
        //console.log(body);
        if (response.status !== 200) throw Error(body.message);
        this.setState({doneFetching: true});
        return body;
    };

    updateAssignments() {
        this.getAssignments()
          .then(res => this.setState({ assignmentsArr: res.arr.assignments }))
          .catch(err => console.log(err));
    }

    handleDisabledClick(event) {
        alert("عليك إختيار مهمة أولًا");
    }

    deleteAssignment(id) {
        fetch(`/api/assigns/${id}`, {
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            method: "DELETE",
        }).then(window.location.reload());
        //return response.json();
    }

    chooseAssign(event, assign, assignId) {
        event.preventDefault();
        let time = (assign.secs).toString();
        this.setState({
            chosenAssign: {
                sents: assign.sents,
                secs: time
            },
            doneChoosing: true,
            chosenId: assignId
        });
    }

    askAgain(event, id) {
        event.preventDefault();
        if (window.confirm("هل أنت متأكد/ة؟ سيتم حذف هذه المهمة كليًا")) {
            this.deleteAssignment(id);
          }
    }

    deleteAll() {
        fetch('/api/assigns', {
            headers : { 
              'Content-Type': 'application/json',
              'Accept': 'application/json'
             },
            method: "DELETE",
        }).then(window.location.reload());
        //return response.json();
    }

    askAgainAll(event) {
        event.preventDefault();
        if (this.state.assignmentsArr.length === 0) {
            return
        }
        if (window.confirm("هل أنت متأكد/ة؟ سيتم حذف جميع المهام كليًا ولن تتمكن/ي من استرجاعهن")) {
            this.deleteAll();
          }
    }

    render() {
    const list = this.state.assignmentsArr;
    return (
    <>
        <Info page='home2' />
        <RefreshPage />
        <div className="App">
            <h1>المهام السابقة</h1>
            <button className="nav-btn" onClick={this.askAgainAll} style={{width:"150px", height:"50px", color:"red"}}>حذف جميع المهام</button>
            {this.state.doneChoosing ? 
            <Link to={{
                    pathname: './list',
                    state: {
                        assignment: this.state.chosenAssign,
                        back: 2
                    }
                }}>
                    <button className="nav-btn" style={{width:"150px", height:"50px"}} variant="raised">
                        العبور الى صفحة الطلاب
                    </button>
                </Link> : 
                <button className="nav-btn" style={{width:"150px", height:"50px"}} onClick={this.handleDisabledClick} variant="raised">
                    العبور الى صفحة الطلاب
                </button>
            }
            <Link to={"./"}>
                <button className="nav-btn" style={{width:"150px", height:"50px"}} variant="raised">
                    الرجوع
                </button>
            </Link>
            <hr></hr>
        </div>
        {this.state.doneFetching ? 
            <div style={{overflow: "auto"}}>
                {list.length > 0 ?
                    <>
                    {list.map((assignm, index) => {
                        return (
                            <div key={index}>
                                <BuildAssign assign={assignm.assignment} />
                                <button className="home2-choose-btn" style={{fontSize:"18px", width:"60px", height:"30px"}} onClick={(e) => this.chooseAssign(e, assignm.assignment, assignm.id)}>إختيار</button>
                                <button style={{fontSize:"18px", width:"60px", height:"30px", color: "red"}} onClick={(e) => this.askAgain(e, assignm.id)}>حذف</button>
                                <Link to={{
                                    pathname: './home',
                                    state: {
                                        assign: assignm.assignment,
                                        _id: assignm.id,
                                        back: './home2'
                                    }
                                }}>
                                    <button style={{fontSize:"18px", width:"60px", height:"30px"}}>تعديل</button>
                                </Link>
                                {(this.state.chosenId === assignm.id) && <p className="home2-deleted-p" style={{color:"red"}}>لقد تم إختيار المهمة, للتقدم إضغط/ي على زر العبور الى صفحة الطلاب</p>}
                                <hr></hr>
                            </div>
                        )
                    })} </> :
                    <span style={{marginLeft:"50%"}}>لا يوجد مهام سابقة</span>}
            </div> :
            <span style={{marginLeft:"50%"}}>الرجاء الإنتظار, اذا لم يتم ظهور المهام إضغط/ي على زر التحديث أعلاه</span>
    }
    </>
    );
    }
}
export default Home2;