import React from 'react';

class AnimatedSent extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            words: [],
            idInterval: 0,
            i: -1,
            colors: ["red", "yellow", "blue", "green", "purple", "pink", "white", "brown", "orange", "rgb(0, 189, 57)", "rgb(56, 233, 109)", "rgb(122, 92, 179)", "rgb(190, 34, 120)"],
            chosen_color: "",
            hide_start_btn: false,
            fSize: "80px"
        }

        this.handleStart = this.handleStart.bind(this);
        this.incrementI = this.incrementI.bind(this);
        this.chooseRandColor = this.chooseRandColor.bind(this);
        this.getColor = this.getColor.bind(this);
        this.increaseSize = this.increaseSize.bind(this);
        this.decreaseSize = this.decreaseSize.bind(this);
        this.handleStop = this.handleStop.bind(this);
    }

    static getDerivedStateFromProps(props, state) {
        let splitted = props.sentence.split(" ");
        return {
            words: splitted
        };
    }

    chooseRandColor() {
        let color = this.state.colors[(Math.random() * this.state.colors.length) >> 0];
        this.setState({
            chosen_color: color
        });
    }

    increaseSize() {
        let curSize = parseInt(this.state.fSize);
        if (curSize < 200) {
            curSize += 10;
            curSize = curSize.toString() + "px";
            this.setState({
                fSize: curSize
            })
        }
    }

    decreaseSize() {
        let curSize = parseInt(this.state.fSize);
        if (curSize > 10) {
            curSize -= 10;
            curSize = curSize.toString() + "px";
            this.setState({
                fSize: curSize
            })
        }
    }

    handleStart() {
        this.chooseRandColor();
        let idVar = setInterval(this.incrementI, 1000*this.props.time);
        this.props.parentCallBack(true);
        this.setState({
            idInterval: idVar,
            i: 0,
            hide_start_btn: true
        });
    }

    handleStop() {
        clearInterval(this.state.idInterval);
        this.props.parentCallBack(false);
        let j = -1;
        this.setState({
            hide_start_btn: false,
            i: j
        });
    }

    incrementI() {
        let j = this.state.i;
        if (j <= this.state.words.length - 2) {
            j += 1;
            this.setState({
                i: j
            });
        }
        else{
            this.handleStop();
        }
    }

    getColor(index) {
        //let index = this.state.words.indexOf(word);
        if (index === this.state.i) {
            return this.state.chosen_color;
        }
        return 'black';
    }

    render() {
        return (
            <>
                <div>
                    {this.state.words.map((word, ind) => {
                        return (
                            //<>
                                <span key={ind} style={{ fontSize:this.state.fSize, color: this.getColor(ind) }}>{word} </span>
                            //</>
                        );
                    })}
                    { !this.state.hide_start_btn && <button className="start-btn" onClick={this.handleStart}>إبدأ</button>}
                </div>
                <div style={{ marginTop: "70px" }}>
                    <button  className="pls-mns-ps-btns" onClick={this.increaseSize}>➕</button>
                    <button  className="pls-mns-ps-btns" onClick={this.decreaseSize}>➖</button>
                    {this.state.hide_start_btn && <button className="pls-mns-ps-btns" onClick={this.handleStop}>❚❚</button>}
                </div>
            </>
        );
    }

}

export default AnimatedSent;