import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import AnimatedSent from '../Components/AnimatedSent';
import Info from '../Components/Info';

class List extends Component {
  // Initialize the state
  constructor(props){
    super(props);
    this.state = {
        curSentence: "",
        sents: [],
        secs: "",
        done: false,
        i: -1,
        playing: false,
        back: 1
    };

    this.nextSentence = this.nextSentence.bind(this);
    this.prevSentence = this.prevSentence.bind(this);
  }

  // Fetch the list on first mount
  componentDidMount() {
    //this.getList();
    let assign = this.props.location.state.assignment;
    let goBack = this.props.location.state.back;
    //console.log(assign);
    let ass_sents = assign.sents;
    let ass_secs = assign.secs;
    this.setState({
        sents: ass_sents,
        secs: ass_secs,
        back: goBack
    }, () => { this.nextSentence() });
  }

  // Retrieves the list of items from the Express app
  getList = () => {
    fetch('/api/getAssign')
    .then(res => res.json())
    .then(list => this.setState({ list }))
  }

  handleCallBack = (childIsAnimating) => {
      this.setState({
          playing: childIsAnimating
      });
  }

  nextSentence(event) {
    if(!this.state.playing){
        let j = this.state.i;
        if (j < this.state.sents.length - 1) {
            j += 1;
            let curSent = this.state.sents[j];
            this.setState({
                curSentence: curSent,
                i: j
            });
        }
    }
  }

  prevSentence(event) {
      if(!this.state.playing) {
        let j = this.state.i;
        if (j > 0) {
            j -= 1;
            let curSent = this.state.sents[j];
            this.setState({
                curSentence: curSent,
                i: j
            });
        }
    }
  }

  render() {
    //const { assignment } = this.props.location.state;
    const list = this.state.sents;

    return (
        <>
        <Info page="list" />
        <div className="App">
          <header>
              {/*<h1 style={{ display:"inline-block" }}>صفحة الطلاب</h1>*/}
              {this.state.back === 2 ?
              <Link to='./home2'>
              <button className="nav-btn" style={{ float: "right" }}>الرجوع</button>
              </Link> :
              <Link to={{
                  pathname: './home',
                  state: {
                      assign: {
                          sents: [],
                          secs: 0
                      },
                      _id: -1,
                      back: './'
                  }
              }}>
              <button style={{ float: "right" }}>الرجوع</button>
              </Link>}
          </header>
          {/* Check to see if any items are found*/}
          {list.length ? (
            <div style={{ marginTop:"150px" }}>
              {/* Render the list of items */}
              {/*list.map((item) => {
                return(
                  <div>
                    {item}
                  </div>
                );
              })*/
              <>
                  {/*<p>{this.state.curSentence}</p>*
                  <button onClick={this.handleStart}>إبدأ</button>*/}
                  <AnimatedSent parentCallBack={this.handleCallBack} sentence={this.state.curSentence} time={this.state.secs} />
                  <div style={{marginTop:"30px"}}>
                      <button className="list-btn" onClick={this.nextSentence}>الجملة التالية</button>
                      <button className="list-btn" onClick={this.prevSentence}>الجملة السابقة</button>
                  </div>
              </>
              }
            </div>
          ) : (
            <div>
              <h2>الرجاء المحاولة من جديد, هنالك خطأ ما</h2>
            </div>
          )
        }
        </div>
      </>
    );
  }
}

export default List;