import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Info from '../Components/Info';

class Home0 extends Component {

    constructor(props) {
        super(props);
        this.state = {
            prev_assignments: []
        }

        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(event) {

    }

    render() {
    return (
    <>
        <Info page="home0" />
        <div className="App">
            <h1>صفحة المعلمة</h1>
            <Link to={{
                pathname: "./home",
                state: {
                    assign: {
                        sents: [],
                        secs: 0
                    },
                    _id: -1,
                    back: './'
                }
                }}>
                <button className="home0-btn" variant="raised">
                    إنشاء مهمة جديدة
                </button>
            </Link>
            <Link to={"./home2"}>
                <button onClick={this.handleClick} className="home0-btn" variant="raised">
                    مهام سابقة
                </button>
            </Link>
        </div>
    </>
    );
    }
}
export default Home0;