import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Info from '../Components/Info';

class Home extends Component {

    constructor(props) {
        super(props);
        this.state = {
            sentences: [],
            curSentence: "",
            toContinue: false,
            time: "",
            timeToShow: "",
            done: false,
            goBack: './',
            intervalId: 0,
            originalAssignment: {
                sents: [],
                secs: ""
            },
            originalId: -1
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleDelete = this.handleDelete.bind(this);
        this.handleKeyUp = this.handleKeyUp.bind(this);
        this.handleDeleteAll = this.handleDeleteAll.bind(this);
        this.handleContinue = this.handleContinue.bind(this);
        this.handleTimeInputChange = this.handleTimeInputChange.bind(this);
        this.handleTimeBtnClick = this.handleTimeBtnClick.bind(this);
        this.handleTimeInputKeyUp = this.handleTimeInputKeyUp.bind(this);
        this.checkChange = this.checkChange.bind(this);
        this.editAssign = this.editAssign.bind(this);
        this.handleSaveAssign = this.handleSaveAssign.bind(this);
        this.drag = this.drag.bind(this);
        this.drop = this.drop.bind(this);
        this.switchDragged = this.switchDragged.bind(this);
        this.switchUpwards = this.switchUpwards.bind(this);
        this.switchDownwards = this.switchDownwards.bind(this);
    }

    componentDidMount() {
        let sentsArr = this.props.location.state.assign.sents;
        let sec = (this.props.location.state.assign.secs).toString();
        let Back = this.props.location.state.back;
        let id = this.props.location.state._id;
        if (sentsArr.length > 0) {
            this.setState({
                sentences: sentsArr,
                time: sec,
                timeToShow: sec,
                toContinue: true,
                goBack: Back,
                done: true,
                dragging: false,
                draggedInd: -1,
                droppedInd: -1,
                originalAssignment: {
                    sents: sentsArr,
                    secs: sec
                },
                originalId: id
            });
        }
    }

    addSent(){
        if (this.state.curSentence !== "") {
            let sents = this.state.sentences;
            sents.push(this.state.curSentence.trim());
            this.setState({ sentences: sents });
        }
    }

    handleKeyUp(event) {
        if (event.key === 'Enter' || event.keyCode === 13) {
            this.addSent();
            this.setState({ curSentence: "" });
        }
    }

    handleChange(event) {
        this.setState({ curSentence: event.target.value });
    }

    handleClick(event) {
        this.addSent();
        this.setState({ curSentence: "" });
    }

    handleDelete(event, item) {
        let sents = this.state.sentences;
        const index = sents.indexOf(item);
        sents.splice(index, 1);
        this.setState({ sentences: sents});
    }

    handleDeleteAll(event) {
        this.setState({ sentences: [] });
    }

    handleContinue(event) {
        if (this.state.sentences.length) {
            this.setState({ toContinue: true });
        }
    }

    handleTimeInputChange(event) {
        this.setState({ time: event.target.value });
    }

    handleTimeInputKeyUp(event) {
        if (event.key === 'Enter' || event.keyCode === 13) {
            if (this.checkTime()) {
                this.saveAssignment();
            }
        }
    }

    checkTime() {
        if (isNaN(this.state.time)) {
            alert("الرجاء إدخال رقم");
            this.setState({
                time: "",
                timeToShow: ""
            });
            return false;
        }
        // eslint-disable-next-line
        if (this.state.time == 0 || this.state.time < 0) {
            alert("الرجاء إدخال رقم أكبر من صفر");
            this.setState({
                time: "",
                timeToShow: ""
            });
            return false;
        }
        let t = this.state.time;
        this.setState({timeToShow: t});
        return true;
    }

    saveAssignment() {
        this.setState({
            done: true 
        })
    }

    handleTimeBtnClick(event) {
        if (this.checkTime()) {
            this.saveAssignment();
        }
    }

    handleClickDisabled(event) {
        alert("عليك إنشاء المهمة أولًا");
    }
    
    handleSaveAssign = async e => {
        e.preventDefault();
        var time = parseInt(this.state.time);
        const response = await fetch('/api/assigns', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ 
                sents: this.state.sentences,
                secs: time }),
            });
        const body = await response.text();
        alert(body);
        //console.log(body);
    };

    checkChange() {
            //console.log("time:");
            //console.log((this.state.originalAssignment.secs).toString());
            //console.log((this.state.time).toString());
            //console.log("length:");
            //console.log((this.state.originalAssignment.sents.length).toString());
            //console.log((this.state.sentences.length).toString());
            if (this.state.originalAssignment.secs !== this.state.time) {
                return true;
            }
            if (this.state.originalAssignment.sents.length !== this.state.sentences.length) {
                return true;
            }
            for (let i=0; i<this.state.originalAssignment.sents.length; i++) {
                if (this.state.originalAssignment.sents[i] !== this.state.sentences[i]) {
                    //console.log((i).toString());
                    return true;
                }
            }
            //console.log("nothing");
            return false;
        }

    editAssign = async e => {
        e.preventDefault();
        const response = await fetch(`/api/assigns/${this.state.originalId}`, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ 
                sents: this.state.sentences,
                secs: this.state.time }),
            }).then(this.setState({
                originalAssignment: {
                    sents: this.state.sentences,
                    secs: this.state.time
                }
            }));
        const body = await response.text();
        alert(body);
        //console.log(body);
    };

    drag(e, index) {
        this.setState({dragging: true, draggedInd: index});
        setTimeout(() => {this.setState({dragging: false});}, 5000);
    }

    switchUpwards() {
        let lowI = this.state.droppedInd;
        let highI = this.state.draggedInd;
        let sentsArr = this.state.sentences;
        let tmp1 = sentsArr[lowI];
        let tmp2;
        sentsArr[lowI] = sentsArr[highI];
        let i = lowI + 1;
        while (i < highI) {
            tmp2 = sentsArr[i];
            sentsArr[i] = tmp1;
            tmp1 = tmp2;
            i += 1;
        }
        sentsArr[highI] = tmp1;
        this.setState({
            sentences: sentsArr,
            dragging: false
        });
    }

    switchDownwards() {
        let highI = this.state.droppedInd;
        let lowI = this.state.draggedInd;
        let sentsArr = this.state.sentences;
        let tmp1 = sentsArr[highI];
        let tmp2;
        sentsArr[highI] = sentsArr[lowI];
        let i = highI - 1;
        while (i > lowI) {
            tmp2 = sentsArr[i];
            sentsArr[i] = tmp1;
            tmp1 = tmp2;
            i -= 1;
        }
        sentsArr[lowI] = tmp1;
        this.setState({
            sentences: sentsArr,
            dragging: false
        });
    }

    switchDragged() {
        if (this.state.draggedInd === this.state.droppedInd) {
            return
        }
        if (this.state.draggedInd < this.state.droppedInd) {
            this.switchDownwards();
        }
        else {
            this.switchUpwards();
        }
    }

    drop(e, index) {
        e.preventDefault();
        this.setState({droppedInd: index}, () => {
            this.switchDragged();
        });
    }

    allowDrop(e) {
      e.preventDefault();
    }

    render() {
    const borderStyle = this.state.dragging ? "1px solid white" : "none";
    const pageProp = this.state.goBack === './home2' ? "home-b" : "home-a";
    return (
    <>
        <Info page={pageProp} />
        <div className="App">
        {this.state.goBack === './home2' ? <h1>تعديل مهمة سابقة</h1> :
        <h1>إنشاء مهمة جديدة</h1>}
        {/* Link to List.js */}
        {this.state.done ?
            <Link to={{
                pathname: './list',
                state: {
                    assignment: {
                        sents: this.state.sentences,
                        secs: this.state.time
                    },
                    back: 1
                }
            }}>
                <button className="nav-btn" style={{width:"150px", height:"50px"}} variant="raised">
                    العبور الى صفحة الطلاب
                </button>
            </Link> :
            <button className="nav-btn" style={{width:"150px", height:"50px"}} onClick={this.handleClickDisabled} variant="raised">
                العبور الى صفحة الطلاب
            </button>
        }
        <Link to={this.state.goBack}>
            <button className="nav-btn" style={{width:"150px", height:"50px"}} variant="raised">
                الرجوع
            </button>
        </Link>
        </div>
        <div>
            <button className="enter-sen-btn" onClick={this.handleClick}>إدخال</button>
            <input dir="rtl" type="text" value={this.state.curSentence} placeholder="أدخل/ي جملة" onKeyUp={this.handleKeyUp} onChange={this.handleChange} className="teacher-input"></input>
        </div>
        {this.state.sentences.length ? 
        <>
          <div>
            <hr></hr>
            {/* Render the list of sentences */}
            {this.state.sentences.map((item, index) => {
              return(
                <div key={index} style={{marginTop:"20px", marginLeft:"50%", fontSize:"18px"}}>
                  <span id={"span"+(index).toString()} onDrop={(e)=>this.drop(e,index)} onDragOver={this.allowDrop} style={{ width:"100px", border: borderStyle }}><span style={{ cursor:"pointer" }} id={"drag"+(index).toString()} draggable="true" onDragStart={(e)=>this.drag(e,index)}>{item}</span></span>
                  <button className="home-sents-edit-btn" style={{ color: "red" }} onClick={(e)=>this.handleDelete(e, item)}>
                      حذف
                  </button>
                </div>
              );
            })}
            <button className="home-sents-edit-btn" style={{color: "red", marginTop:"20px", marginLeft:"50%"}} onClick={this.handleDeleteAll}>
                حذف جميع الجمل
            </button>
            <button className="home-sents-edit-btn" onClick={this.handleContinue}>
                التقدم
            </button>
          </div>
            {this.state.toContinue ? 
            <>
                <div style={{marginTop:"15px", marginLeft:"50%"}}>
                    <input dir="rtl" style={{fontSize:"18px"}} type="text" value={this.state.time} placeholder="أدخل/ي الوقت بالثواني" onKeyUp={this.handleTimeInputKeyUp} onChange={this.handleTimeInputChange}></input>
                    <button className="home-sents-edit-btn" onClick={this.handleTimeBtnClick}>إدخال الوقت</button>
                </div> 
                {this.state.timeToShow !== "" ? <div style={{marginTop:"15px", marginLeft:"50%"}}>
                    <span>لقد تم إختيار </span>{this.state.timeToShow}<span> بالثوانٍ, لإختيار رقم آخر أعيد/ي الإدخال</span>
                    {this.state.goBack === "./" ?
                    <button title="إضغط/ي هنا اذا أردت حفظ المهمة للإستعمالات اللاحقة" className="home-sents-edit-btn" style={{display:"block", marginTop:"5px"}} onClick={this.handleSaveAssign}>إحفظ/ي المهمة</button> :
                    <>
                    <button title="حفظ المهمة مع إبقاء المهمة السابقة" className="home-sents-edit-btn" style={{display:"block", marginTop:"5px"}} onClick={this.handleSaveAssign}>الحفظ كمهمة جديدة</button>
                    <button title="حفظ المهمة مكان المهمة السابقة" className="home-sents-edit-btn" style={{display:"block", marginTop:"5px"}} onClick={this.editAssign}>تعديل المهمة</button>
                    </>
                    }
                </div>: <></>}
            </>:
                <></>
            }
        </>
          : 
          <div>
            <h2 className="no-sents-entered" style={{marginLeft:"40%", marginTop:"100px"}}>لم يتم إدخال جمل حتى الآن</h2>
          </div>
      }
    </>
    );
    }
}
export default Home;